<script>
import moment from "moment";
import _ from "lodash";
import axios from "axios";
import Swal from "sweetalert2";

export default {
  name: "CusCountView",
  props: ["ipAdress", "transaksi"],

  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      jtrx: Object.values(this.transaksi.semua_mutasi_sebelumnya).filter(
        (trx) => trx.status != "FAILED"
      ).length,
      nowDate: null,
      lastDate: new Date(2022, 11, 31),
      lastDateConvert: null,
      currentDate: null,
      diffDays: null,
    };
  },

  methods: {
    numberFormat(x) {
      if (x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      } else {
        return (x = "0");
      }
    },
    format_date(value) {
      if (value) {
        return moment(String(value)).format("DD MMMM YYYY HH:mm");
      }
    },
    getNow: function () {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      this.nowDate = dateTime;

      const oneDay = 24 * 60 * 60 * 1000; // hours*minutes*seconds*milliseconds
      const lastDate = this.lastDate;
      const currentDate = new Date();

      const month = [
        "Januari",
        "Februari",
        "Maret",
        "April",
        "Mei",
        "Juni",
        "Juli",
        "Agustus",
        "September",
        "Oktober",
        "November",
        "Desember",
      ];
      this.lastDateConvert =
        lastDate.getDate() +
        " " +
        month[lastDate.getMonth()] +
        " " +
        lastDate.getFullYear();
      this.currentDate = currentDate;

      const diffDays = Math.round((lastDate - currentDate) / oneDay);
      this.diffDays = diffDays + 1;
    },
  },

  created() {
    setInterval(this.getNow, 1000);
  },
};
</script>

